import DeleteForeverOutlined from "@mui/icons-material/DeleteForeverOutlined";
import { useEffect, useState } from "react";
import { useMiddleware } from "../../../Application";
import ConfirmDialog from "../../../components/ConfirmDialog";


const CampaignDeleteDialog = ({
    open,
    onClose,
    campaign,
    onDelete,
    requestConfig,
    ...confirmProps
}) => {

    const middleware = useMiddleware('Customers-Club.Campaigns');

    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (open) {
            setLoading(false);
        }
    }, [open])

    const deleteAsync = async () => {
        setLoading(true);
        try {
            const response = await middleware.delete(campaign?.id, requestConfig);
            setLoading(false);

            if (response.ok) onDelete();
        } catch (err) {
            console.log(err)
        }
    }


    return (
        <ConfirmDialog
            open={open}
            onClose={onClose}
            {...confirmProps}
            title={`ارسال گروهی حذف گردد؟`}
            content={`ارسال گروهی برای همیشه حذف گردد؟`}
            onConfirm={deleteAsync}
            confirmButtonProps={{ color: 'error' }}
            cancelButtonProps={{ autoFocus: true }}
            confirmText={`حذف برای همیشه`}
            loading={loading}
            confirmIcon={<DeleteForeverOutlined />}
        />
    )
}

export default CampaignDeleteDialog