import AddIcon from "@mui/icons-material/Add";
import MenuOutlined from "@mui/icons-material/MenuOutlined";
import {
    Box, 
    Card, 
    // Divider, 
    Stack
} from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
// import { useSnackbar } from "notistack";
import { useSearchParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
// import useDialogProps from "../../../../utils/useDialogProps";
// import useNavigateTask from "../../../../utils/useNavigateTask";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
// import PersonDeleteDialog from "../../components/PersonDeleteDialog";
import DataTable from "./DataTable";
import { useSnackbar } from "notistack";
import useNavigate from "../../../../utils/useNavigate";
import { useEffect } from "react";
import useDialogProps from "../../../../utils/useDialogProps";
import CampaignDeleteDialog from "../../components/CampaignDeleteDialog";
// import FilterForm from "./FilterForm";


const Campaigns = () => {

    const { inject } = useAuth();

    const middleware = useMiddleware('Customers-Club.Campaigns');

    const [searchParams] = useSearchParams();
    const navigate = useNavigate();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    })

    useEffect(() => {
        if(!loading && total < 1) navigate('add'); 
    }, [data, total, loading])

    const { enqueueSnackbar } = useSnackbar();

    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["person"]
    });
    const handleDelete = () => {
        enqueueSnackbar('ارسال گروهی حذف گردید');
        closeDeleteDialog();
        reload();
    }

    const runAsync = async (campaign) => {
        // setLoading(true);
        try {
            const response = await middleware.run(campaign?.id);
            // setLoading(false);

            if (response.ok) {
                enqueueSnackbar('ارسال گروهی اجرا گردید');
                reload();
            }
        } catch (err) {
            console.log(err)
        }
    }

    const injectedHooks = Object.assign({}, ...(inject('Customers-Club', 'CampaignsList.Hooks') || []));

    const task = useTask('Customers-Club.campaigns');


    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Box
                    sx={{
                        display: "flex",
                        mb: { xs: 0, sm: 1 }
                    }}
                >
                    <Box sx={{ flexGrow: 1 }} />
                    <Stack direction="row" spacing={1}>
                        <ResponsiveActionBar
                            actions={sortBy([
                                {
                                    title: 'ارسال گروهی جدید',
                                    icon: <AddIcon />,
                                    onClick: () => navigate("add"),
                                    authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit'),
                                    priority: 100
                                },
                                ...flatten((inject('Customers-Club', 'CampaignsList.ActionsList', { data, loading, reload, ...injectedHooks }) || []))
                            ], o => o.priority)}
                            ResponsiveActionIcon={<MenuOutlined />}
                            responsiveActionProps={{ color: 'primary' }}
                        />
                    </Stack>
                </Box>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    {/* <FilterForm /> */}
                    {/* <Divider /> */}
                    <DataTable
                        data={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                        onClickRun={runAsync}
                        onClickDelete={(campaign) => {
                            openDeleteDialog({ campaign });
                        }}
                    />
                </Card>

                <CampaignDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                />

                {inject('Customers-Club', 'CampaignsList.Footer', { data, loading, reload, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}


export default Campaigns