import { Box, Button, DialogContent, Grid, Fab as MuiFab, Slide, Stack, styled, useMediaQuery, useTheme } from "@mui/material";
import { useNavigate } from "react-router";
import { Authorized } from "../auth";
import useHideOnScroll from "../utils/useHideOnScroll";
import useLocationHash from "../utils/useLocationHash";
import IconButton from "./IconButton";
import ResponsiveDialog from "./ResponsiveDialog";


const Fab = styled(MuiFab)(({ theme }) => ({
    position: 'fixed',
    bottom: `calc(${theme.spacing(2)} + 48px)`,
    left: theme.spacing(2),
    zIndex: 100
}));

const Navbar = styled(Box)(({ theme }) => ({
    //
}));

const IconContainer = styled('span')(({ theme }) => ({
    border: "1px solid currentColor",
    borderRadius: '50%',
    padding: theme.spacing(.75),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
}));

const ResponsiveActionBar = ({
    actions = [],
    ResponsiveActionComponent = Fab,
    responsiveActionProps = {},
    ResponsiveActionIcon,
    showResponsiveOnDesktop = false
}) => {

    const theme = useTheme();
    const isResponsive = useMediaQuery(theme.breakpoints.down('sm'));

    const navigate = useNavigate();
    const segmentName = 'actions';
    const { hasSegment, addSegment, removeSegment } = useLocationHash();

    const isActionComponentHidden = useHideOnScroll();

    const ResponsiveActionComponentContainer = ({ children }) => actions.length > 1 ? (<>{children}</>) : (
        <Slide direction="up" in={!isActionComponentHidden} mountOnEnter unmountOnExit>{children}</Slide>
    )


    return showResponsiveOnDesktop || isResponsive ? (
        <>
            <ResponsiveActionComponentContainer>
                <ResponsiveActionComponent
                    onClick={actions.length > 1 ? () => navigate(addSegment(segmentName)) : actions[0]?.onClick}
                    {...responsiveActionProps}
                >
                    {actions.length > 1 ? ResponsiveActionIcon : actions[0]?.icon}
                </ResponsiveActionComponent>
            </ResponsiveActionComponentContainer>
            {actions.length > 0 && (
                <ResponsiveDialog
                    dialogProps={{
                        fullWidth: true,
                        maxWidth: "xs"
                    }}
                    open={hasSegment(segmentName)}
                    onClose={() => navigate(removeSegment(segmentName), { replace: true })}
                >
                    <DialogContent>
                        <Grid container columns={3}>
                            {actions.map((action, index) => {
                                return (
                                    <Authorized key={index} authorize={action.authorize}>
                                        <Grid item xs={1} alignItems="center" justifyContent="center">
                                            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                                <IconButton
                                                    label={action.title}
                                                    onClick={action.onClick}
                                                    size="large"
                                                    color="inherit"
                                                    labelTypographyProps={{
                                                        sx: { mt: 1 }
                                                    }}
                                                >
                                                    <IconContainer>
                                                        {action.icon}
                                                    </IconContainer>
                                                </IconButton>
                                            </div>
                                        </Grid>
                                    </Authorized>
                                )
                            })}
                        </Grid>
                    </DialogContent>
                </ResponsiveDialog>
            )}
        </>
    ) : (
        <Navbar>
            <Stack direction={isResponsive ? "row-reverse" : "row"} spacing={1}>
                {actions.map((action, index) => (
                    <Authorized key={index} authorize={action.authorize}>
                        <Button
                            variant="transparent"
                            disableElevation
                            startIcon={action.icon}
                            onClick={action.onClick}
                            {...action.buttonProps}
                        >
                            {action.title}
                        </Button>
                    </Authorized>
                ))}
            </Stack>
        </Navbar>
    )
}

ResponsiveActionBar.propTypes = {
    // ResponsiveActionIcon: PropsTypes.element.isRequired
}
export default ResponsiveActionBar;