import { Icon } from "@iconify/react";
import {
    Box,
    Chip,
    ListItemButton, ListItemText
} from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";

import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import isFuture from "date-fns-jalali/isFuture";

const DataTable = ({
    loading,
    data,
    meta,
    infiniteScrollProps,
    onClickRun,
    onClickDelete,
}) => {

    const { inject } = useAuth();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();


    return (
        <>
            <ResponsiveTable
                data={data}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>,
                        priority: 100
                    }, {
                        field: 'content',
                        title: 'متن پیام',
                        rowCellProps: {
                            width: 200
                        },
                        render: row => <Box whiteSpace="pre-line"><Text>{row.message?.content}</Text></Box> || '-',
                        priority: 200
                    }, {
                        title: 'گروه‌های هدف',
                        rowCellProps: {
                            width: 300
                        },
                        renderCell: row => (
                            <Text>{row.targets.map(r => r.target.name).join(', ')}</Text>
                        ),
                        priority: 300
                    }, {
                        title: 'وضعیت پیام',
                        rowCellProps: {
                            width: 50
                        },
                        renderCell: row => (
                            (() => {
                                let props = {};
                                switch(row.message.latestStatus.status) {
                                    case "pending":
                                        props = { label: "در حال تایید", color: "warning" }
                                        break;
                                    case "confirmed":
                                        props = { label: "تایید شده", color: "success" }
                                        break;
                                    case "rejected":
                                        props = { label: "رد شده", color: "error" }
                                        break;
                                    default:
                                        props = { label: "نامشخص", color: "default" }
                                }

                                return <Chip {...props} size="small"/>
                            })()
                        ),
                        priority: 300
                    }, {
                        title: 'وضعیت ارسال',
                        headerCellProps: {
                            align: 'center',
                            sx: {
                                whiteSpace: 'nowrap'
                            }
                        },
                        rowCellProps: {
                            width: 100
                        },
                        renderCell: row => (
                            (() => {
                                let props = {};

                                if(row.message.latestStatus.status !== "confirmed") {
                                    return null;
                                } 
                                
                                if(!row.shouldRunAt) {
                                    props = { label: "آماده ارسال", color: "info" }
                                }

                                if(row.shouldRunAt) {
                                    if(isFuture(new Date(row.shouldRunAt))) {
                                        props = { label: "در حال ارسال", color: "info" }
                                    } else {
                                        props = { label: "ارسال شده", color: "success" }
                                    }
                                }

                                return <Chip {...props} size="small"/>
                            })()
                        ),
                        priority: 300
                    },
                    ...flatten((inject('Customers-Club', 'CampaignsList.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <Box whiteSpace="pre-line"><Text>{row.message?.content}</Text></Box>}
                renderListItemChildren={(row) => (
                    <ListItemButton>
                        <ListItemText primary={
                            <Box whiteSpace="pre-line">
                                <Text>{row.message?.content}</Text>
                            </Box>
                        }/>
                    </ListItemButton>
                )}

                tableRowProps={row => ({ striped: true })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    row => row.message.latestStatus.status === "confirmed" ? ({
                        title: 'ارسال',
                        variant: 'button',
                        icon: <Icon icon="solar:map-arrow-left-bold" />,
                        onClick: onClickRun,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('campaign:run')
                    }) : undefined,
                    {
                        title: 'حذف',
                        icon: <Icon icon="solar:trash-bin-minimalistic-2-bold"/>,
                        onClick: onClickDelete,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('campaign:delete'),
                        componentProps: { color: "error" }
                    }
                ]}
            />
        </>
    )
}

export default DataTable